import interfaces from '../../../utils/interface.js'
export default{
	data(){
		return{
			inp_text : ''
		}
	},
	methods:{
		goBack(){
			uni.navigateBack()
		},
		seek() {
			this.request({
				url: interfaces.search, //搜索
				method: "POST",
				data: {
					goods_name: this.inp_text
				},
				success: ((res) => {
					if (res.error_code == 0) {
						uni.navigateTo({
							url: "./toCategory?goodsInfo=" + JSON.stringify(res.data)
						})
					}
					else{
						uni.showToast({
							icon : 'none',
							title:  res.data
						})
					}
				})
			});
		}
	}
}
